.formGroup {
  font-size: 13px;
}

.labelColor {
  color: #a8b0bc;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 12px;
}
.inputColor {
  border-radius: 15px !important;
  font-size: 15px !important;
}
.addNewAttachmentBlock {
  display: flex;
  justify-content: space-between;
  padding-left: 6px;
  padding-right: 30px;
}
.addNewAttachments {
  color: #3b93e9;
  text-decoration: underline;
  font-size: 14px;
  display: contents;
  letter-spacing: 1px;
}
.addNewAttachments:hover {
  cursor: pointer;
}
.error {
  border: 2px solid red !important;
  box-shadow: none !important;
}

.uploadedFormButton {
  border-radius: 15px;
  border: 1px solid #ced4da;
  padding: 30px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.removeFileIcon {
  position: relative;
  right: 35px;
  cursor: pointer;
}
.uploadRow {
  text-align: center;
  margin-top: 20px;
}

.dragDropContent {
  font-size: 10px;
  letter-spacing: 2px;
  color: #a8b0bc;
}
.dragDropContent span {
  background-color: #96d1ff;
  color: white;
  padding: 5px 10px;
  border-radius: 10px;
}

.uploadButton {
  padding: 6px 30px !important;
  font-size: 14px !important;
  border-radius: 30px !important;
  background-color: rgb(38, 112, 184) !important;
  letter-spacing: 2px;
  font-weight: 500 !important;
}
.formStyle {
  margin-left: 0px;
}

/*  by default above css for extra small size */
@media only screen and (min-width: 576px) {
  .formStyle {
    margin-left: 35px;
  }
  .labelColor {
    font-size: 17px;
  }
  .uploadButton {
    font-size: 20px !important;
  }
}

/*  given css is for tablet screen */
@media only screen and (min-width: 768px) {
  .labelColor {
    font-size: 24px;
  }
  .inputColor {
    font-size: 20px !important;
  }
}

/*  given css is for laptop screen */
@media only screen and (min-width: 992px) {
  .formStyle {
    width: 46rem;
    margin-left: 0px;
  }
  .labelColor {
    font-size: 16px;
  }
  .uploadRow {
    text-align: unset;
  }
}
/*  given css is for desktop screen */
@media only screen and (min-width: 1200px) {
  .formStyle {
    width: 46rem;
  }
  .labelColor {
    font-size: 16px;
  }
  .uploadRow {
    text-align: center;
  }
}
